// BLOCKS *****************************************************

.wrapper {
    @include wrapper();
}

.row-flex {
    @include row-flex();
}

html {
    background: #CDCDCD;
    font-size: 10px;
}

body {
    max-width: 1920px;
    margin: 0 auto;
    position: relative;
    background-color: #ffffff;
}

a {
    cursor: pointer;
}

.contact {
    text-align: right;

    &_phone {
        font-size: 1.6rem;
        line-height: 2.2rem;
        font-family: "MullerMedium";
    }

    &_adress {
        font-size: 1.4rem;
        line-height: 1.4rem;
        font-family: "MullerMedium";
        opacity: 0.6;
    }
}

//HEADER********************************************

.header {
    width: 100%;
    padding-bottom: 120px;

    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        &_left {
            display: flex;
            align-items: center;
        }

        &_right {
            display: flex;
            align-items: center;
        }

    }

    &_inside {
        background: url("../images/header-bg.jpg") no-repeat;
        //background-size: contain;
    }

    &_top {
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        height: 116px;
        background: #FFFFFF;
        padding-top: 30px;
    }

    &_nav {
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: 30px;
        @media(max-width: 670px){
            height: 0px;
        }
    }

    &_title {
        margin-bottom: 29px;
        @include font-size(7.2rem);
    }

    &_text {
        font-family: "MullerMedium";
        font-size: 2.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.02em;
        margin-bottom: 5.0rem;

    }

    &_content {
        @include size(5);
        @include padding-top(16.3rem);
    }

    &_phone {
        color: #000000;
    }

    &_btn {
        margin-bottom: 217px;
    }

    &_top-btn {
        margin-left: 40px;
    }
    &_logo{
        margin-right: 80px;
        background:url("../images/logo-blue-desktop.png") no-repeat;
        width: 166px;
        height: 56px;

    }
    &_contact{
        @media(max-width: 670px){
            display: none;
        }
        &-mobile{
            display: none;
        }
    }

}

@media(max-width: 1300px) {
    .header {
        &_btn {
            margin-bottom: 100px;
        }
        &_top {
            height: auto;
            padding-bottom: 20px;
        }

        .wrapper {
            //justify-content: flex-end;
            &_left {
               width: 100%;
               justify-content: space-between;
            }

            &_right {
                width: 100%;
                justify-content: flex-end;
            }
        }
    }
}
@media(max-width: 900px) {
    .header {
        &_nav {
            //align-self: flex-start;
            margin-right: 0;
        }
        &_btn {
            margin-bottom: 100px;
        }

        &_top {
            height: auto;
            padding-bottom: 20px;
        }
        &_logo{
            align-self: center;
            margin-right: 0;
        }
        &_content{
            width: 100%;
        }

        .wrapper {
            flex-direction: column;
            //justify-content: flex-end;
            &_left {
                flex-wrap: wrap;
                flex-direction: column;
            }

            &_right {
                width: auto;
                justify-content: flex-start;
            }
        }
    }
}
@media(max-width:670px){
    .header {
        .wrapper {
            flex-direction: row;
            position: relative;
        }
        .wrapper_left {
            width: auto;
        }
        &_logo{
            margin-left: 95px;
        }

    }

}

.advantages {
    @include row-flex();
    align-items: center;
    height: 180px;

    &_item {
        display: flex;
        align-items: start;
        justify-content: space-around;
        @include col();
        @include size(3);
    }

    &_title {
        font-family: "MullerMedium", "sans-serif";
        //font-size: 2.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.02em;
         @include font-size(2.4rem)
    }

    &_text {
        font-family: "MullerMedium", sans-serif;
        font-size: 1.4rem;
        line-height: 120%;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
    }

    &_description {
        width: 65%;
    }
}

.advantages-wrapper {
    background: #FFFFFF;
    box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
    border-radius: 5px;
}

@media (max-width:1470px) {
    .advantages{
        height: 200px;
        &_item{
            width: calc(50% - 30px);
        }
    }
}
@media (max-width:1300px) {
    .advantages {
        &_description {
            width: 100%;
            margin-left: 20px;
        }
    }
}

@media (max-width:880px) {
    .advantages {
        height: auto;
        padding-top: 25px;
        padding-left: 20px;
        &_item {
            width: 100%;
            margin-bottom: 25px;
        }

    }
}
/********MENU BLOCK****************/
.menu {
    //@include size(7);
    &_list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 40px;
        width: 630px;

        @media(max-width:670px) {
            display: none;
            position: relative;
        }

        &--drop {
            @include xs-block() {
                display: block;
            }
        }

        &>li {
            margin-right: 10px;
            height: 30px;

            &:hover {
                border-bottom: 2px solid $primary;
            }
        }
    }

    li {
        display: block;
        flex-shrink: 0;

        &:hover {
            opacity: 1;
        }
    }

    &_toggle {
        background-color: transparent;
        display: none;
        height: 18px;
        width: 24px;
        cursor: pointer;
        @media (max-width:670px) {
            display: block;
            position: absolute;
            left: 15px;
            top: 10px;
        }
    }

    .last {
        margin-right: 0px;
    }

    &_item-1 {
        position: relative;
    }

    &_item-5 {
        position: relative;
    }

    a {
        color: #000000;
        opacity: 0.6;
        display: block;
        font-size: 1.6rem;
        font-family: "MullerMedium";
        line-height: 22px;
        letter-spacing: 0.15px;
        transition: all .2s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }
}

li.menu_item-1 {
    @include xs-block() {
        padding: 0;
        width: 230px;
    }
}

.menu_item-1 {
    a {
        display: flex;
        align-items: center;
    }

    span {
        display: block;
        color: $primary;
    }
}

.menu_item-1:hover {
    background-color: #ffffff;
    @include xs(padding, 0 0 0 10px);

    span {
        transform: rotateZ(180deg);
    }

    .menu_drop-1 {
        display: block;
    }
}

.menu_drop-1 {
    z-index: 1000;
    position: absolute;
    top: 30px;
    width: 210px;
    background-color: #ffffff;
    display: none;
    box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
    border-radius: 5px;

    @include xs-block() {
        position: static;
    }

    li {
        padding-bottom: 5px;
        margin-right: 0;
        padding-left: 20px;
        border-bottom: solid 1px #EDEDEE;
        transition: all .2s ease-in-out;

        &:hover {
            background: $primary;

            a {
                color: white;
            }
        }

    }

    li:last-child {
        border-bottom-width: 0px;
    }

    a {
        line-height: 36px;
    }
}
svg#menu {
    width: 24px;
}

/************ END OF MENU BLOCK**********/


// SERVICE BLOCK******************************************
.service {
    @include wrapper();
    height: 660px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 140px;

    &_about {
        width: 57%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &_feedback {
        width: 40%;
    }

    &_subtitle {
        font-family: "MullerBold";
        font-size: 24px;
        line-height: 87%;
        letter-spacing: 0.02em;
    }

    &_text {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.15px;
        color: #F5F5F5;
    }

    &_discription,
    &_price-discription {
        font-size: 1.8rem;
        line-height: 22px;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
    }

    &_price-discription li {
        line-height: 32px;
    }

    &_danger {
        display: flex;

        &-text {
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin-left: 20px;

            p:nth-of-type(1) {
                font-family: MullerBold;
                font-size: 2.4rem;
                line-height: 2.4rem;
                letter-spacing: 0.02em;
            }

            p:nth-of-type(2) {
                font-family: MullerMedium;
                font-size: 1.6rem;
                line-height: 1.9rem;
                letter-spacing: 0.02em;
                color: rgba(0, 0, 0, 0.6);
            }
        }
    }

    &_price {
        //background: url(../images/Group26.jpg) no-repeat, $primary;
        background-size: cover;
        background-color: $primary;
        height: 135px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 50px;
        margin-bottom: 20px;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;

        p:nth-of-type(1) {
            font-size: 2.4rem;
            line-height: 24px;
            letter-spacing: 0.02em;
            color: #FFFFFF;
            background-color: transparent;
            margin-bottom: 8px;
        }

        p:nth-of-type(2) {
            font-size: 1.6rem;
            line-height: 19px;
            letter-spacing: 0.02em;
            color: rgba(255, 255, 255, 0.6);
            background-color: transparent;
        }
    }

    &_form {
        background: #FFFFFF;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;
        padding: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        hr {
            width: 100%;
            height: 1px;
            background: #D6D6D6;
            transform: matrix(1, 0, 0, -1, 0, 0);
            margin-bottom: 39px;
        }

        &-field {
            margin-bottom: 20px;
            margin-right: 10px;
        }


        &-agreement {
            width: 57%;
            padding-top: 5px;
            margin-top: 50px;

            a {
                color: $primary;
            }
        }

        button {
            width: 184px;
            margin-top: 50px;
        }
    }


}

.feedback-form {
    &_title {
        font-family: MullerMedium, sans-serif;
        font-size: 2.4rem;
        line-height: 24px;
        letter-spacing: 0.02em;
        margin-bottom: 8px;
    }

    &_subtitle {
        font-family: MullerMedium, sans-serif;
        font-size: 1.6rem;
        line-height: 19px;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
        margin-bottom: 40px;
    }

    input {
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
