        @include padding-top(10rem);
        padding-right: 10px;
        padding-bottom: 10px;
         @include padding-left(14.5rem);
        margin-left: -50px;
        z-index: 1;

        h3 {
            margin-left: 25px;
            margin-bottom: 23px;
        }
    }

    &_img {
        width: 41.3%;
