        position: relative;
    }
}

.accordion {
    height: 373px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 6px;
        background: #CDD4E0;
        border-radius: 27px;
    }

    &::-webkit-scrollbar-thumb {
        background: #0545BA;
        border-radius: 27px;
    }

    &_item {
        margin-right: 25px;
        margin-left: 25px;
        margin-bottom: 11px;

        &--active {
            .accordion_text {
                max-height: 135px;
                padding-top: 22px;
                padding-left: 26px;
                padding-bottom: 22px;
                transition: max-height .4s linear;

                p {
                    max-height: 100%;
                    transition: max-height .4s linear;
                }
            }

            .accordion_item-header {
                background: #0545BA;
                color: #ffffff;

                span {
                    transform: rotate(180deg);
                    transition: transform .4s linear;
                }
            }
        }

        &-header {
            display: flex;
            align-items: center;
            padding-left: 25px;
            background: #ffffff;
            border-radius: 5px;
            border: 2px solid #0545BA;
            color: $primary;
            height: 52px;
        }

        h5 {
            margin-left: 10px;
        }

        span {
            font-size: 2.5rem;
        }
    }

    &_text {
        width: 100%;
        padding: 0;
        background: #FFFFFF;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        max-height: 0;
        overflow: hidden;

        p {
            width: 65%;
            text-align: justify;
            line-height: 17px;
            max-height: 0;
            overflow: hidden;
        }
    }
}
@media(max-width:1300px){
    .accordion{
        &_text p{
            width: 95%;
        }
    }
}
@media(max-width:970px) {
    .accordion {
        height: auto;
        overflow-y: auto;
    }
}
@media(max-width:970px) {
